import { Component, OnInit } from '@angular/core';
import { SessionService } from 'src/app/core/session/session.service';
import { WorkPermitService } from 'src/app/api/controllers/WorkPermit';
import moment from 'moment';

@Component({
  selector: 'app-workpermit-widget',
  templateUrl: './workpermit-widget.component.html',
  styleUrls: ['./workpermit-widget.component.scss']
})
export class WorkpermitWidgetComponent implements OnInit {
  role:string;
  workPermit:any = [];
  totalItems:number = 0;
  isDataLoaded:boolean = true;

  constructor( 
    private sessionService:SessionService,
    private workpermitService: WorkPermitService
  ) { }

  get isAdmin() {
    return this.sessionService.isAdmin();
  }

  getDateFormat(date) {
    return moment(date).format('ddd MMM D YYYY')
  }

  getTimeFormat(time) {
    return moment(time).format('hh:mm A')
  }

  ngOnInit(): void {
    if(this.isAdmin) {
      let entity = {
        apartmentId:  this.sessionService.apartmentId,
        active: 1,
      }
      this.workpermitService.getWorkPermitsByApartmentIdStatus(entity).subscribe((res:any) => {
        if (res.length > 0) {
          this.workPermit = res.reverse().slice(0,9);
          this.totalItems = this.workPermit.length;
        }
        this.isDataLoaded = false;
      })
    } 
    else {
      let params = {
        apartmentId:  this.sessionService.apartmentId,
        apartmentUnitId: parseInt(this.sessionService.apartmentBlockUnitID),
      }
      this.workpermitService.getWorkPermitsByApartmentIdUnitId(params).subscribe((res:any) => {
        if (res.length > 0) {
          this.workPermit = res.reverse().slice(0,9);
          this.totalItems = this.workPermit.length;
        }
        this.isDataLoaded = false;
      })
    } 
  }

}
