import { Component, OnInit } from '@angular/core';
import { AlertService } from 'src/app/api/controllers/Alert';
import { SessionService } from 'src/app/core/session/session.service';
import moment from 'moment';

@Component({
  selector: 'app-emergency-alerts-widget',
  templateUrl: './emergency-alerts-widget.component.html',
  styleUrls: ['./emergency-alerts-widget.component.scss']
})
export class EmergencyAlertsWidgetComponent implements OnInit {
  emergencyAlert:any = [];
  role:string;
  totalItems:number = 0;
  isDataLoaded:boolean = true;

  constructor(
    private alertService: AlertService,
    private sessionService:SessionService,
  ) { }

  get isAdmin() {
    return this.sessionService.isAdmin();
  }

  getDateFormat(date) {
    return moment(date).format('ddd MMM D YYYY')
  }

  getTimeFormat(time) {
    return moment(time).format('hh:mm A')
  }

  getBgStatus(id) {
    if(id == 135) {  //open
      return 'bg-status-purple-700';
    } else if (id == 137) {  //closed
      return 'bg-status-green-700';
    } else if(id == 136) { //inprogress
      return 'bg-status-orange-700'
    } else if(id == 1) {
      return 'bg-status-red-700';
    }
  }

  getTextStatus(id) {
    if(id == 135) {  //open
      return 'text-status-purple-900';
    } else if (id == 137) {  //completed
      return 'text-status-green-900';
    } else if (id == 136) {  //inprogress
      return 'text-status-orange-900';
    } else if(id == 1) {
      return 'text-status-red-900';
    }
  }

  ngOnInit(): void {
    if(this.isAdmin) {
      let entity = {
        apartmentId:  this.sessionService.apartmentId,
        active: 1,
      }
      this.alertService.getAllApartmentBlockUnitAlertByApartmentIdStatus(entity).subscribe((res:any)=>{
        if (res.length > 0) {
          this.emergencyAlert = res.reverse().slice(0,5);
          this.totalItems = this.emergencyAlert.length;
        }
        this.isDataLoaded = false;
      })
    } 
    else {
      let params = {
        apartmentBlockUnitId : this.sessionService.apartmentBlockUnitID
      }
      this.alertService.getAllApartmentBlockUnitAlertByApartmentBlockUnitId(params).subscribe((res:any)=>{
        if (res.length > 0) {
          this.emergencyAlert = res.reverse().slice(0,5);
          this.totalItems = this.emergencyAlert.length;
        }
        this.isDataLoaded = false;
      })
    } 
  }

}
