import { Component, OnInit, ViewChild, TemplateRef, ViewEncapsulation } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { DashBoardService } from 'src/app/api/controllers/DashBoard';
import { SessionService } from 'src/app/core/session/session.service';
import { TicketService } from 'src/app/api/controllers/Ticket';

import * as moment from 'moment';
import _moment from "moment-timezone";
import { RouterOutlet, Router, ActivatedRoute, ActivationStart } from '@angular/router';

@Component({
  selector: 'app-admin-widget-two',
  templateUrl: './admin-widget-two.component.html',
  styleUrls: ['./admin-widget-two.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class AdminWidgetTwoComponent implements OnInit {
  @ViewChild("datePopup") datePopup: TemplateRef<any>;
  @ViewChild("dashboardReportRef", { static: false }) dashboardReportRef: TemplateRef<any>;

  @ViewChild(RouterOutlet) outlet: RouterOutlet;
  filterField:any = {};
  isFilterSubmit:boolean = false;
  currentClick:string;

  timeZone:any;

  widgetData:any = [
    {
      front : 'Unapproved Pending Admins',
      back : 'Unapproved Pending Users',
      frontFooter: true,
      backFooter: true,
      frontValue: '',
      backValue: '',
      type: 'unapproved',
      isFlip: true
    },{
      front : 'Open Community Tickets',
      back : 'Open Personal Tickets',
      frontFooter: true,
      backFooter: true,
      frontValue: '',
      backValue: '',
      type: 'ticket',
      isFlip: true
    },
    {
      front : 'Unassigned Tickets',
      back : '',
      frontFooter: true,
      backFooter: true,
      frontValue: '',
      backValue: '',
      type: 'helpdesk',
      isFlip: false
    },
    {
      front : 'Open Panic Alerts',
      back : 'Inprogrss Panic Alerts',
      frontFooter: true,
      backFooter: true,
      frontValue: '',
      backValue: '',
      type: 'alerts',
      isFlip: true
    },
  ];

  constructor( public dialog: MatDialog,
    private dashboardService:DashBoardService,
    private ticketService: TicketService,
    private sessionService:SessionService,
    private _activatedRoute: ActivatedRoute,
    private _router: Router,) {
      this._router.events.subscribe(e => {
        if (e instanceof ActivationStart && e.snapshot.outlet === "primary")
          this.outlet.deactivate();
      });
     }


  flip(data) {
    let front = data.firstElementChild;
    let back = data.lastElementChild;
    if(front.classList.contains('flip-bk') && back.classList.contains('flip-ft')) {
      front.classList.remove('flip-bk');
      back.classList.remove('flip-ft');
    } else {
      front.classList.add('flip-bk');
      back.classList.add('flip-ft');
    }
  }

  yesterday(type) {
    let entity:any = {};
    entity.fromDate = moment(new Date()).subtract(1, 'days').utc().format();
    entity.toDate = _moment().toISOString();
    return entity;
  }

  today(type) {
    let entity:any = {};
    entity.fromDate = _moment().toISOString();
    entity.toDate = _moment().toISOString();
    return entity;
  }

  filter(filterMode,name,type) {
    this.currentClick = type;
    let result = filterMode  == 'yesterday' ? this.yesterday(type) : this.today(type);
    if(name == 'Pending Admins') {
      this.unapprovedPendingAdmins(result);
    } else if(name == 'Pending Users') {
      this.unapprovedPendingUsers(result);
    } else if(name == 'Open Community') {
      this.openCommunityTickets(result);
    } else if(name == 'Open Personal') {
      this.openPersonalTickets(result);
    } else if (name == 'InProgress Panic Alerts') {
      this.inProgressPanicAlerts(result);
    } else if (name == 'Open Panic Alerts') {
      this.openPanicAlerts(result);
    }
  }

  calenderPopUp(name,type) {
    this.currentClick = type;
    const dialogRef = this.dialog.open(this.datePopup, {
      panelClass: 'material-dialog-medium',
    });
    dialogRef.afterClosed().subscribe(result => {
      if(this.isFilterSubmit) {
        if(this.currentClick == 'alerts') {
         let date  = this.filterField.fromDate.toISOString();
         if(name == 'Open Panic Alerts')
          this.openPanicAlerts({date:date});
         else if(name == 'Inprogrss Panic Alerts')
          this.inProgressPanicAlerts({date:date});
        } else {
          this.filterField.fromDate = this.filterField.fromDate.toISOString();
          this.filterField.toDate = this.filterField.toDate.toISOString();
          if(name == 'Pending Admins' && type == 'unapproved')
            this.unapprovedPendingAdmins(this.filterField);
          else if(name == 'Pending Users' && type == 'unapproved')
            this.unapprovedPendingUsers(this.filterField);
          else if(name == 'Open Community' && type == 'ticket') 
            this.openCommunityTickets(this.filterField);
          else if(name == 'Open Personal' && type == 'ticket') 
            this.openPersonalTickets(this.filterField);
        }
        this.filterField = {};
        this.isFilterSubmit = false;
      }
    });
  }

  applyFilter() {
    this.isFilterSubmit = true;
    this.dialog.closeAll();
  }

  unapprovedPendingAdmins(result) {
    let entity = {
      fromDate : result.fromDate,
      toDate : result.toDate,
      apartmentId : this.sessionService.apartmentId,
    }
    this.dashboardService.getDashUnapprovedUsersPendingAdminbyApartmentId(entity).subscribe((res:any)=>{
      this.widgetData[0].frontValue = res.unapprovedUsersPendingAdmin;
    })
  }

  unapprovedPendingUsers(result) {
    let entity = {
      fromDate : result.fromDate,
      toDate : result.toDate,
      apartmentId : this.sessionService.apartmentId,
    }
    this.dashboardService.getDashUnapprovedUsersPendingusersbyApartmentId(entity).subscribe((res:any)=>{
      this.widgetData[0].backValue = res.unapprovedPendingusers;
    })
  }

  openCommunityTickets(result) {
    let entity = {
      fromDate : result.fromDate,
      toDate : result.toDate,
      apartmentId : this.sessionService.apartmentId,
    }
    this.dashboardService.getDashOpencommticketsbyApartmentId(entity).subscribe((res:any)=>{
      this.widgetData[1].frontValue = res.openCommunitytickets;
    })
  }

  openPersonalTickets(result) {
    let entity = {
      fromDate : result.fromDate,
      toDate : result.toDate,
      apartmentId : this.sessionService.apartmentId,
    }
    this.dashboardService.getDashOpenpersonalticketsbyApartmentId(entity).subscribe((res:any)=>{
      this.widgetData[1].backValue = res.openPersonalticket;
    })
  }

  unassignedTickets() {
    const params = {
      ApartmentID: this.sessionService.apartmentId,
      ApartmentBlockID: this.sessionService.apartmentBlockID,
      StartDate: "2020-01-01",
      EndDate: moment(new Date()).format("YYYY-MM-DD").toString(),
      TicketStatus: null,
      TicketType: null,
      TicketRaisedBy: null,
      AssignedToStaffId: null,
      StaffCategoryType: null,
      StaffSubCategoryType: null,
    }
    this.ticketService.getDashUnassignedTicketsbyApartmentId(params).subscribe((res:any)=>{
      this.widgetData[2].frontValue = res;
    })
  }

  inProgressPanicAlerts(result) {
    let params = {
      fromDate : result.date,
      apartmentId: this.sessionService.apartmentId
    }
    this.dashboardService.getDashinProgressPanicAlertsbyApartmentId(params).subscribe((res:any)=>{
      this.widgetData[3].frontValue = res.inProgressPanicAlerts;
    })
  }

  openPanicAlerts(result) {
    let params = {
      fromDate : result.date,
      apartmentId: this.sessionService.apartmentId
    }
    this.dashboardService.getDashOpenPanicAlertsbyApartmentId(params).subscribe((res:any)=>{
      this.widgetData[3].backValue = res.openPanicAlerts;
    })
  } 
  

  ngOnInit(): void {
    let entity = {
      apartmentId : this.sessionService.apartmentId,
      fromDate : _moment().subtract(5,'months').startOf('month').toISOString(),
      toDate : _moment().toISOString()
    }
    this.unapprovedPendingAdmins(entity);
    this.unapprovedPendingUsers(entity);
    this.openCommunityTickets(entity);
    this.openPersonalTickets(entity);
    this.unassignedTickets();
    let params = {
      apartmentId : this.sessionService.apartmentId,
      date : moment(new Date("2020-01-01")).format("MM/DD/YYYY") // _moment().subtract(10,'months').startOf('month').toISOString()
    }
    this.inProgressPanicAlerts(params);
    this.openPanicAlerts(params)
  }

  detailInfo(data, item) {
    let dialogRef = this.dialog.open(this.dashboardReportRef, {
      panelClass: 'material-dialog-big'
    })

    dialogRef.afterOpened().subscribe((res:any) => {
      if(item === "Open Community Tickets"){
        let name = "List of tickets";
        this._router.navigate([`/ams/dashboard/main/helpdesk/reports/${name}/362/true/24/32`]);
      } else if(item === "Open Personal Tickets"){
        let name = "List of tickets";
        this._router.navigate([`/ams/dashboard/main/helpdesk/reports/${name}/362/true/27/32`]);
      } else if(item === "Unassigned Tickets"){
        let name = "List of Unassigned tickets";
        this._router.navigate([`/ams/dashboard/main/helpdesk/reports/${name}/364/true`]);
      } else if(item === "Open Panic Alerts"){
        let name = "List of Open Panic Alerts";
        this._router.navigate([`/ams/dashboard/main/security/reports/${name}/135/true`]);
      } else if(item === "Inprogrss Panic Alerts"){
        let name = "List of Inprogrss Panic Alerts";
        this._router.navigate([`/ams/dashboard/main/security/reports/${name}/136/true`]);
      }  else if(item === "Unapproved Pending Admins"){
      let name = "List of Unapproved Unit Users";
      this._router.navigate([`/ams/dashboard/main/unit&user/reports/${name}/2026/true`]);
    } else if(item === "Unapproved Pending Users"){
      let name = "List of Unapproved Unit Users";
      this._router.navigate([`/ams/dashboard/main/unit&user/reports/${name}/2027/true`]);
    }
    })

    dialogRef.afterClosed().subscribe((res:any) => {
      this._router.navigate(['/ams/dashboard/main'], {relativeTo : this._activatedRoute});
      
    })

  

  }
}
