import { Component, OnInit, ViewEncapsulation, TemplateRef, ViewChild } from '@angular/core';
import { SessionService } from 'src/app/core/session/session.service';
import { DashBoardService } from 'src/app/api/controllers/DashBoard';
import { WorkPermitService } from 'src/app/api/controllers/WorkPermit';

import * as moment from 'moment';
import _moment from "moment-timezone";
import { ActivatedRoute, Router, ActivationStart, RouterOutlet } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'app-admin-widget-three',
  templateUrl: './admin-widget-three.component.html',
  styleUrls: ['./admin-widget-three.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class AdminWidgetThreeComponent implements OnInit {

  @ViewChild("dashboardReportRef", { static: false }) dashboardReportRef: TemplateRef<any>;

  @ViewChild(RouterOutlet) outlet: RouterOutlet;

  widgetData:any = [
    {
      front : 'Visitors Checked-in',
      back : 'Visitors Checked-out',
      frontFooter: false,
      backFooter: false,
      frontValue: '',
      backValue: '',
      type: 'visitors',
      isFlip: true
    },{
      front : 'Visitors Not Checked',
      back : '',
      frontFooter: false,
      backFooter: false,
      frontValue: '',
      backValue: '',
      type: 'visitor-checked',
      isFlip: false
    },
    {
      front : 'Payment Defaulters Customers',
      back : 'No of Credit Notes',
      frontFooter: false,
      backFooter: false,
      frontValue: '',
      backValue: '',
      type: 'accounts',
      isFlip: true
    },
    {
      front : 'Pending Vendor Payments',
      back : '',
      frontFooter: false,
      backFooter: false,
      frontValue: '',
      backValue: '',
      type: 'vendor',
      isFlip: true
    },

    {
      front : 'Facility Booking Approved',
      back : 'Facility Booking UnApproved',
      frontValue: '',
      backValue: '',
      frontFooter: false,
      backFooter: false,
      type: 'facility',
      isFlip: true
    },
    {
      front : 'Unapproved Move In',
      back : 'Upcoming Approved Move In',
      frontValue: '',
      backValue: '',
      frontFooter: false,
      backFooter: false,
      type: 'movein',
      isFlip: true
    },
    {
      front : 'Unapproved Move Out',
      back : 'Upcoming Approved Move Out',
      frontValue: '',
      backValue: '',
      frontFooter: false,
      backFooter: false,
      type: 'movein',
      isFlip: true
    },
    {
      front : 'Workpermits Pending Approval',
      back : 'Upcoming Workpermits',
      frontValue: '',
      backValue: '',
      frontFooter: false,
      backFooter: false,
      type: 'workpermit',
      isFlip: true
    }
  ];

  constructor(
    private sessionService:SessionService,
    private dashboardService:DashBoardService,
    private workPermitService:WorkPermitService,
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    public dialog: MatDialog,
  ) {
    this._router.events.subscribe(e => {
      if (e instanceof ActivationStart && e.snapshot.outlet === "primary")
        this.outlet.deactivate();
    });
   }

  flip(data) {
    let front = data.firstElementChild;
    let back = data.lastElementChild;
    if(front.classList.contains('flip-bk') && back.classList.contains('flip-ft')) {
      front.classList.remove('flip-bk');
      back.classList.remove('flip-ft');
    } else {
      front.classList.add('flip-bk');
      back.classList.add('flip-ft');
    }
  }

  visitorsCheckedIn(result) {
    let params = {
      startDate : result.startDate,
      EndDate : result.EndDate,
      apartmentId: this.sessionService.apartmentId
    }
    this.dashboardService.getDashVisitorsCheckedinbyApartmentId(params).subscribe((res:any)=>{
      this.widgetData[0].frontValue  = res.visitorsCheckIn;
    });
  }

  visitorsCheckedOut(result) {
    let params = {
      startDate : result.startDate,
      EndDate : result.EndDate,
      apartmentId: this.sessionService.apartmentId
    }
    this.dashboardService.getDashVisitorsCheckedouttodaybyApartmentId(params).subscribe((res:any)=>{
      this.widgetData[0].backValue  = res.visitorsCheckedouttoday;
    });
  }

  visitorsNotChecked() {
    let entity = {
      apartmentId: this.sessionService.apartmentId
    }
    this.dashboardService. getDashVisitorsNotcheckedtodaybyApartmentId(entity).subscribe((res:any)=>{
      this.widgetData[1].frontValue  = res.visitorsNotcheckedtoday;
    });
  }

  getPaymentDefaulters() {
    let entity = {
      apartmentId :  this.sessionService.apartmentId
    }
    this.dashboardService.getDashpaymentdefaultersasoftodayybyApartmentId(entity).subscribe((res:any)=>{
      this.widgetData[2].frontValue =  res.returnDefautlers;
    });
  }

  getCreditNote() {
    let entity = {
      apartmentId :  this.sessionService.apartmentId
    }
    this.dashboardService.getDashcreditnoteappliedbyApartmentId(entity).subscribe((res:any)=>{
      this.widgetData[2].backValue =  res.creditNotesCount;
    });
  }

  // New Row

  facilityApproved() {
    let entity = {
      apartmentId :  this.sessionService.apartmentId
    }
    this.dashboardService. getDashufacilitybookingapprovedtodaybyApartmentId(entity).subscribe((res:any)=>{
      this.widgetData[4].frontValue = res.approvedfaclitiesTodayCount;
    });
  
  }

  facilityUnApproved() {
    let entity = {
      apartmentId :  this.sessionService.apartmentId
    }
    this.dashboardService.getDashufacilitybookingunapprovedbyApartmentId(entity).subscribe((res:any)=>{
      this.widgetData[4].backValue = res.unapprovedfaclities;
    });
  }

  unApproveMoveIn(result) {
    let entity = {
      date : result.date,
      apartmentId : this.sessionService.apartmentId,
    }
    this.dashboardService.getDashunapprovedmoveinbyApartmentId(entity).subscribe((res:any)=>{
      this.widgetData[5].frontValue = res.unapprovedMoveIn;
    })
  }

  upcomingMoveIn(result) {
    let entity = {
      fromDate : result.startDate,
      toDate : result.EndDate,
      apartmentId : this.sessionService.apartmentId,
    }
    this.dashboardService.getDashupcomingmoveinApartmentId(entity).subscribe((res:any)=>{
      this.widgetData[5].backValue = res.upcomingWeeklyApprovedMoveInCounts;     
    });
  }


  unApproveMoveOut(result) {
    let entity = {
      date : result.date,
      apartmentId : this.sessionService.apartmentId,
    }
    this.dashboardService.getDashunapprovedmoveoutbyApartmentId(entity).subscribe((res:any)=>{
      this.widgetData[6].frontValue = res.unapprovedMoveOut;
    })
  }
 
  upcomingMoveOut(result) {
    let entity = {
      fromDate : result.startDate,
      toDate : result.EndDate,
      apartmentId : this.sessionService.apartmentId,
    }
    this.dashboardService.getDashupcomingmoveoutApprovedbyApartmentId(entity).subscribe((res:any)=>{
      this.widgetData[6].backValue = res.upcomingMoveOut;
    });
  }

  workpermitsPendingApproval() {
    let entity = {
      apartmentId: this.sessionService.apartmentId,
      status: 354,
    }
    this.workPermitService.getDashUnapprovedWorkpermitbyApartmentId(entity).subscribe((res:any)=>{
      this.widgetData[7].frontValue = res;
    })
  }

  upcomingWorkPermits() {
    let entity = {
      apartmentId: this.sessionService.apartmentId,
      statusIds: '353,354,355,356,357,358,359',
      StartDate: _moment().toISOString(),
      EndDate:_moment().toISOString(),
    }
    this.workPermitService.getAllWorkPermitsByStatues(entity).subscribe((res:any)=>{
      this.widgetData[7].backValue = res.length;
    })
  }

  ngOnInit(): void {
    let entity = {
      apartmentId : this.sessionService.apartmentId,
      startDate : _moment(new Date("2020-01-01")).format("MM/DD/YYYY"),
      EndDate : _moment().toISOString()
    }
    this.visitorsCheckedIn(entity);
    this.visitorsCheckedOut(entity);
    this.visitorsNotChecked();
    this.getPaymentDefaulters();
    this.getCreditNote();
    // New Row
    this.facilityApproved();
    this.facilityUnApproved()
    let params = {
      date : _moment().toISOString(),
    }
    this.unApproveMoveIn(params);
    this.upcomingMoveIn(entity);
    this.unApproveMoveOut(params);
    this.upcomingMoveOut(entity);
    this.workpermitsPendingApproval();
    this.upcomingWorkPermits();
  }

  detailInfo(data, item) {
    let dialogRef = this.dialog.open(this.dashboardReportRef, {
      panelClass: 'material-dialog-big'
    })

    dialogRef.afterOpened().subscribe((res:any) => {
      if(item === "Visitors Checked-in"){
        let name = "List of Checked In Visitors";
        this._router.navigate([`/ams/dashboard/main/visitor/reports/${name}/365/true`]);
      } else if(item === "Visitors Checked-out"){
        let name = "List of Checked out Visitors";
        this._router.navigate([`/ams/dashboard/main/visitor/reports/${name}/366/true`]);
      } else if(item === "Visitors Not Checked"){
        let name = "List of Visitors Not Checked";
        this._router.navigate([`/ams/dashboard/main/visitor/reports/${name}/364/true`]);
      } else if(item === "Facility Booking UnApproved"){
        let name = "Facility booking, approval pending";
        this._router.navigate([`/ams/dashboard/main/facility/reports/${name}/135/true`]);
      } else if(item === "Facility Booking Approved "){
        let name = "List of Booked Facilities";
        this._router.navigate([`/ams/dashboard/main/facility/reports/${name}/135/true`]);
      } else if(item === "Unapproved Move In"){
        let name = "List of Unapproved Move In";
        this._router.navigate([`/ams/dashboard/main/movein/reports/${name}/372/true`]);
      } else if(item === "Unapproved Move Out"){
        let name = "List of Unapproved Move Out";
        this._router.navigate([`/ams/dashboard/main/moveout/reports/${name}/373/true`]);
      } else if(item === "Workpermits Pending Approval"){
        let name = "List of Unapproved Move Out";
        this._router.navigate([`/ams/dashboard/main/moveout/reports/${name}/373/true`]);
      }  else {
      let name = "List of Expiring Rental  Lease Agreements";
      this._router.navigate([`/ams/dashboard/main/report/reports/${name}/338`]);
    }
    })

    dialogRef.afterClosed().subscribe((res:any) => {
      this._router.navigate(['/ams/dashboard/main'], {relativeTo : this._activatedRoute});
      
    })

  

  }
}