import { Component, OnInit } from '@angular/core';
import { ViolationService } from 'src/app/api/controllers/Violation';
import { SessionService } from 'src/app/core/session/session.service';
import moment from 'moment';

@Component({
  selector: 'app-recent-violation-widget',
  templateUrl: './recent-violation-widget.component.html',
  styleUrls: ['./recent-violation-widget.component.scss']
})
export class RecentViolationWidgetComponent implements OnInit {
  violation:any = [];
  totalItems:number = 0;
  isDataLoaded:boolean = true;

  constructor(
    private violationService: ViolationService,
    private sessionService:SessionService,
  ) { }

  getDateFormat(date) {
    return moment(date).format('ddd MMM D YYYY')
  }

  getTimeFormat(time) {
    return moment(time).format('hh:mm A')
  }

  ngOnInit(): void {
    let entity = {
      apartmentId:  this.sessionService.apartmentId,
      active: 1,
    }
    this.violationService.getAllViolationsByStatus(entity).subscribe((res:any)=>{
      if (res.length > 0) {
        this.violation = res.reverse().slice(0,5);
        this.totalItems = this.violation.length;
      }
      this.isDataLoaded = false;
    })
  }

}
