import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { DashBoardService } from 'src/app/api/controllers/DashBoard';
import { SessionService } from 'src/app/core/session/session.service';
import { DateModelComponent } from '../date-model/date-model.component';

import * as moment from 'moment';
import _moment from "moment-timezone";


@Component({
  selector: 'app-dash-broad-cast',
  templateUrl: './dash-broad-cast.component.html',
  styleUrls: ['./dash-broad-cast.component.scss'],
  encapsulation : ViewEncapsulation.None
})
export class DashBroadCastComponent implements OnInit {
  broadCastList = [];
  totalItems:number =0;
  isDataLoaded:boolean;

  constructor(public dialog: MatDialog,
    private dashboardService:DashBoardService,
    private sessionService:SessionService
  ) { }

  getDateFormat(date) {
    return moment(date).format('ddd MMM D YYYY')
  }

  getTimeFormat(time) {
    return moment(time).format('hh:mm A')
  }


  calenderPopUp(dateType) {
    const dialogRef = this.dialog.open(DateModelComponent, {
      panelClass: 'material-dialog-small',
      width: '400px',
      data: dateType
    });
    dialogRef.afterClosed().subscribe(result => {
      if(result) {
        this.announcementList(result);
      }
    });
  }

  announcementList(entity) {
    this.isDataLoaded = true;
    this.dashboardService.getDashlistBroadcastsentbyApartmentId(entity).subscribe((res:any)=>{
      if(res.length > 0) {
        this.broadCastList = res.reverse().slice(0,9);
        this.totalItems = this.broadCastList.length;
      }  
      this.isDataLoaded = false;
    });
  }

  ngOnInit(): void {
    let entity = {
      apartmentId : this.sessionService.apartmentId,
      fromDate : moment(new Date()).subtract(1, 'month').utc().format(),
      toDate : _moment().toISOString()
    }
    this.announcementList(entity);
  }

}
