import { Component, OnInit } from '@angular/core';
import { SessionService } from 'src/app/core/session/session.service';
import { FacilityService } from 'src/app/api/controllers/Facility';

import moment from 'moment';

@Component({
  selector: 'app-facility-widget',
  templateUrl: './facility-widget.component.html',
  styleUrls: ['./facility-widget.component.scss']
})
export class FacilityWidgetComponent implements OnInit {
  facility:any = [];
  totalItems:number = 0;
  isDataLoaded:boolean = true;

  constructor(
    private facilityService: FacilityService,
    private sessionService:SessionService
  ) { }

  getDateFormat(date) {
    return moment(date).format('ddd MMM D YYYY')
  }

  getTimeFormat(time) {
    return moment(time).format('hh:mm A')
  }

  getFacilityBgStatus(id) {
    if(id == 189) {  //Pending
      return 'bg-status-purple-700';
    } else if (id == 188) {  //completed
      return 'bg-status-green-700';
    } else if (id == 208 || id == 385) {  //Cancelled || Rejected
      return 'bg-status-red-700';
    }
  }

  getFacilityTextStatus(id) {
    if(id == 189) {  //Pending
      return 'text-status-purple-900';
    } else if (id == 188) {  //completed
      return 'text-status-green-900';
    } else if (id == 208 || id == 385) {  //Cancelled || Rejected
      return 'text-status-red-900';
    }
  }

  ngOnInit(): void {
    this.isDataLoaded = true;
    let entity = {
      apartmentId:  this.sessionService.apartmentId,
      active: 1,
    }
    this.facilityService.getApartmentFacilityBookingsByApartmentIdStatus(entity).subscribe((res:any) => {
      if (res.length > 0) {
        this.facility = res.reverse().slice(0,9);
        this.totalItems = this.facility.length;
      }
      this.isDataLoaded = false;
    })
  }

}
