import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { DashBoardService } from 'src/app/api/controllers/DashBoard';
import { MatDialog } from '@angular/material/dialog';
import { DateModelComponent } from '../date-model/date-model.component';
import { SessionService } from 'src/app/core/session/session.service';

import * as moment from 'moment';
import _moment from "moment-timezone";

@Component({
  selector: 'app-dash-meeting',
  templateUrl: './dash-meeting.component.html',
  styleUrls: ['./dash-meeting.component.scss'],
  encapsulation:ViewEncapsulation.None
})
export class DashMeetingComponent implements OnInit {
  meetingList = [];
  totalItems:number = 0;
  isDataLoaded:boolean;

  constructor( public dialog: MatDialog,
    private dashboardService:DashBoardService,
    private sessionService:SessionService) { }

  getDateTimeFormat(date) {
    return moment(date).format('ddd MMM D YYYY')
  }

  calenderPopUp(dateType) {
    const dialogRef = this.dialog.open(DateModelComponent, {
      panelClass: 'material-dialog-small',
      width: '400px',
      data: dateType
    });
    dialogRef.afterClosed().subscribe(result => {
      if(result) {
        this.scheduledMeeting(result);
      }
    });
  }

  scheduledMeeting(entity) {
    this.isDataLoaded = true;
    this.dashboardService.getDashlistupcomingmeetingbyApartmentId(entity).subscribe((res:any)=>{
      if(res.length > 0) {
        this.meetingList = res.reverse().slice(0,9);
        this.meetingList.forEach((data)=>{
          data.fromTime = moment(data.fromTime,'HH:mm').format('hh:mm A')
          data.toTime = moment(data.toTime,'HH:mm').format('hh:mm A')
        })
        this.totalItems =  this.meetingList.length;
      }
      this.isDataLoaded = false;
    });
  }

  ngOnInit(): void {
    let entity = {
      apartmentId : this.sessionService.apartmentId,
      fromDate : _moment().toISOString(),
      toDate : moment(new Date()).add(1, 'month').utc().format(),
    }
    this.scheduledMeeting(entity);
  }

}
