import { Component, OnInit, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { SessionService } from 'src/app/core/session/session.service';

@Component({
  selector: 'app-date-model',
  templateUrl: './date-model.component.html',
  styleUrls: ['./date-model.component.scss']
})
export class DateModelComponent implements OnInit {
  dashboard:any = {
    apartmentId : this.sessionService.apartmentId,
  }
  constructor(
    private sessionService:SessionService,
    public dialogRef: MatDialogRef<DateModelComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) { }

  getDashboardStartDate(event) {
    this.dashboard.fromDate = event
  }
  
  getDashboardEndDate(event) {
    this.dashboard.Enddate = event
  }

  onConfirm(): void {
    if(this.data == 'unapprove' || this.data == 'openaged' || this.data == 'panicAlerts' || this.data == 'unassigned') {
      this.dashboard.date = this.dashboard.date.toISOString()
    } else {
      this.dashboard.fromDate = this.dashboard.fromDate.toISOString()
      this.dashboard.toDate = this.dashboard.toDate.toISOString()
    }
    this.dialogRef.close(this.dashboard);
  }

  ngOnInit(): void {

  }

}
