import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, NavigationEnd, Router, Data } from '@angular/router';

import { Subject } from 'rxjs';
import { filter, takeUntil, startWith } from 'rxjs/operators';

import * as moment from 'moment';
import _moment from "moment-timezone";
import { UserService } from 'src/app/api/controllers/User';
import { SessionService } from 'src/app/core/session/session.service';
import { DashBoardService } from 'src/app/api/controllers/DashBoard';
import { SharedService } from 'src/app/shared/services/shared.service';

import * as _ from 'underscore';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {
  isApartmentSelected:boolean = false;
  isCustomAvailable:boolean = false;
  userRole:string = "";
  userName:any;
  customDashBoard:boolean = false;
  dashBoardName:any;
  data:any;
  constructor(
    private _router: Router,
    private _activatedRoute: ActivatedRoute,
    private userService:UserService,
    private sessionService:SessionService,
  	private sharedService:SharedService,
  ) {}
  
  isAdmin(){
    return this.sessionService.isAdmin()
  }

  isSecurity(){
    return this.userRole == "Security" ? true : false;
  }

  isStaff(){
    return this.userRole == "Staff" ? true : false;
  }

  dashboardChange(event) {
    if(event.checked) {
      this._router.navigateByUrl('/ams/dashboard/custom')
    } else {
      this._router.navigateByUrl('/ams/dashboard')
    }
  }

  ngOnInit() { 

    // Subscribe to the resolved route data
    this._activatedRoute.parent.parent.data.subscribe((data: Data) => {
      
      this.data = data.initialData;
      
      let dashboardMenu = this.data.navigation.default.filter( item => {
        return item.id == 'DASHBOARD'
      })

      if(dashboardMenu.length > 0) {
        this.isCustomAvailable = _.some(dashboardMenu[0].children, item => {
          return item.id == 'CUSTOM'
        })
      }
    });

    this._router.events
    .pipe(
        filter((event) => event instanceof NavigationEnd),
        startWith(this._router)
    )
    .subscribe((event: NavigationEnd) => {
      this.customDashBoard = event.url.includes('custom')
      this.dashBoardName = event.url.includes('custom') ? 'Custom' : 'General'
    })

    let params = {
      userid: parseInt(this.sessionService.userId)
    }
    this.userService.getUserById(params).subscribe((res:any) => {
      this.userName = res[0].firstName;
    },
    error => {
    });


  	this.sharedService.apartmentselectedcast.subscribe( isSelected => {
  		this.isApartmentSelected = isSelected;
    });
    

  }

}
