import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { SessionService } from 'src/app/core/session/session.service';
import { DashBoardService } from 'src/app/api/controllers/DashBoard';
import { forkJoin } from 'rxjs';
import { SharedService } from 'src/app/shared/services/shared.service';

@Component({
  selector: 'app-dash-pie-chart',
  templateUrl: './dash-pie-chart.component.html',
  styleUrls: ['./dash-pie-chart.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class DashPieChartComponent implements OnInit {
  chartList:any;
  isDataLoaded:boolean;
  legendPosition: any = { left: 520, top: 140, width: 100, height: 100 };
  padding: any = { left: 5, top: 5, right: 5, bottom: 5 };
  titlePadding: any = { left: 0, top: 0, right: 0, bottom: 10 };
  seriesGroups: any[] = [{
    type: 'donut',
    showLabels: true,
    series:[{
    dataField: 'total',
    displayText: 'cliente',
    labelRadius: 120,
    initialAngle: 20,
    radius: 100,
    innerRadius: 50,
    centerOffset: 0,
    formatSettings: { sufix: '%', decimalPlaces: 1 }}]
  }];

  constructor(
    private sessionService: SessionService,
    private dashboardService: DashBoardService,
    private sharedService: SharedService,
  ) { }

  getWidth() : any {
		return '100%';
  }
  
  ngOnInit(): void {
    this.isDataLoaded = false;
    let params = {
      apartmentId : this.sessionService.apartmentId
    }
    let amtBilled = this.dashboardService.getDashTotalamtbilledcustomersthismonthbyApartmentId(params);
    let amtReceived = this.dashboardService.getDashTotalamtreceivedcustomersthismonthbyApartmentId(params);
    forkJoin([amtBilled,amtReceived]).subscribe((res:any)=>{
      this.isDataLoaded = true;
      if(res.length > 0) {
        let billed, received, unCollected, collectionPercent, unCollectionPercent, chart, amount;
        billed = res[0].totalamtbilledcustomersthismonth;
        received = res[1].totalamtreceivedcustomersthismonth;
        unCollected = billed - received;
        collectionPercent = ((received/billed)*100); 
        unCollectionPercent = ((unCollected/billed)*100); 
        amount = [{ total : collectionPercent, cliente : 'Collected'},{ total : unCollectionPercent, cliente : 'Uncollected'}];
        chart = {
          localdata: amount,
          datatype: "array"
        }
        this.chartList =  new jqx.dataAdapter(chart);
      }
    },(error)=>{
      this.isDataLoaded = true;
      this.sharedService.openSnackBar('Pie Chart Server Error','error');
    })
  }

}
