import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { DashboardComponent } from './dashboard.component';
import { AdminDashboardComponent } from './components/dashboard/admin-dashboard/admin-dashboard.component';
import { CustomDashboardComponent } from './components/dashboard/custom-dashboard/custom-dashboard.component';
import { StaffReportsComponent } from '../staff-manager/components/staff-reports/staff-reports.component';
import { HelpdeskReportsDataComponent } from '../../common/helpdesk/helpdesk-reports/helpdesk-reports-data/helpdesk-reports-data.component';
import { SecurityReportsDataComponent } from '../security/components/security-reports/security-reports-data/security-reports-data.component';
import { VisitorReportsDataComponent } from '../../common/visitor/visitor-report/visitor-reports/visitor-reports-data/visitor-reports-data.component';
import { FacilityReportDataComponent } from '../facility/facility-report/facility-reports/facility-report-data/facility-report-data.component';
import { MoveinReportDataComponent } from '../../common/moveinout-tracker/components/movein-reports/movein-report-data/movein-report-data.component';
import { ParkingManagementReportDataComponent } from '../parking-management/components/parking-management-reports/parking-management-report-data/parking-management-report-data.component';

export const routes: Routes = [
	
  {
    path: '', component: DashboardComponent, children: [
      { path: '', redirectTo: 'main', pathMatch: 'full' },
      {
        path: 'main', component: AdminDashboardComponent, children: [
          { path: 'unit&user', loadChildren: () => import('src/app/modules/ams/unit-users/unit-users-report/unit-users-report.module').then(m => m.UnitUsersReportModule) },
          {
            path: 'staff', loadChildren: () => import('src/app/modules/ams/staff-manager/staff-manager.module').then(m => m.StaffManagerModule),
            children: [
              { path: 'reports/:link', component: StaffReportsComponent },
            ]
          },
          {
            path: 'helpdesk', loadChildren: () => import('src/app/modules/common/helpdesk/helpdesk-reports/helpdesk-reports.module').then(m => m.HelpdeskReportsModule),
            children: [
              { path: 'reports/:name/:id/:link/:type/:status', component: HelpdeskReportsDataComponent },
              { path: 'reports/:name/:id/:link', component: HelpdeskReportsDataComponent },
            ]
          },
          {
            path: 'security', loadChildren: () => import('src/app/modules/ams/security/security.module').then(m => m.SecurityModule),
            children: [
              { path: 'reports/:name/:type/:link', component: SecurityReportsDataComponent },
            ]
          },
          {
            path: 'visitor', loadChildren: () => import('src/app/modules/common/visitor/visitor-report/visitor-report.module').then(m => m.VisitorReportModule),
            children: [
              { path: 'reports/:name/:id/:link', component: VisitorReportsDataComponent },
            ]
          },
          {
            path: 'facility', loadChildren: () => import('src/app/modules/ams/facility/facility-report/facility-report.module').then(m => m.FacilityReportModule),
            children: [
              { path: 'reports/:name/:id/:link', component: FacilityReportDataComponent },
            ]
          },
          {
            path: 'movein', loadChildren: () => import('src/app/modules/common/moveinout-tracker/moveinout-tracker.module').then(m => m.MoveinoutTrackerModule),
            children: [
              { path: 'reports/:name/:id/:link', component: MoveinReportDataComponent },
            ]
          },
          {
            path: 'moveout', loadChildren: () => import('src/app/modules/common/moveinout-tracker/moveinout-tracker.module').then(m => m.MoveinoutTrackerModule),
            children: [
              { path: 'reports/:name/:id/:link', component: MoveinReportDataComponent },
            ]
          },
          {
            path: 'parking', loadChildren: () => import('src/app/modules/ams/parking-management/parking-management.module').then(m => m.ParkingManagementModule),
            children: [
              { path: 'reports/:name/:link', component: ParkingManagementReportDataComponent },
            ]
          }
        ]
      },
      { path: 'custom', component: CustomDashboardComponent }
    ]
  } 
	//typeStatus
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DashboardRoutingModule { }
